.menuItem {
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  border-radius: 15px;
  margin: 0 7.5px 15px;
  background-position: center;
  background-size: cover;
  position: relative;
  overflow: hidden;

  &:hover {
    cursor: pointer;

    & .bgImg {
      transform: scale(1.2);
      transition: transform 4s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }

    & .content {
      opacity: 0.8;
      transition: opacity 2s;
    }
  }

  &.large {
    height: 380px;
  }

  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }

  .bgImg {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    position: absolute;
  }

  .content {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: white;
    opacity: 0.5;
    position: absolute;

    .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
      text-align: center;
    }

    .subtitle {
      text-align: center;
      font-weight: bold;
      font-size: 15px;
    }
  }
}
